import axios from 'axios'
export default {
  name: 'runMission',
  data () {
    return {
      missionList: []
    }
  },
  methods: {
    getMissionList () {
      axios.get(this.url + '/mission/module/' + sessionStorage.getItem('moduleId'))
        .then(this.getMissionListSucc)
    },
    getMissionListSucc (res) {
      res = res.data
      if (res) {
        this.missionList = res
        console.log(this.missionList)
      }
    }
  },
  mounted () {
    this.getMissionList()
  }
}



