import ModuleHeader from './moduleComponents/header'
import ModuleDetail from './moduleComponents/detail/header'
import ModuleMissionHeader from './moduleComponents/mission/header'
import axios from 'axios'

export default {
  name: 'project',
  components: {
    ModuleHeader,
    ModuleDetail,
    ModuleMissionHeader
  },
  data () {
    return {
      module: Object,
      mission: [],
      Name: this.Name
    }
  },
  methods: {
    getModule () {
      sessionStorage.setItem('moduleId', this.$route.params.moId)
      axios.get(this.url + '/module/id/' + this.$route.params.moId)
        .then(this.getModuleSucc)
    },
    getModuleSucc (res) {
      if (res) {
        const data = res.data
        this.module = data
        this.mission = data.missions
        this.Name = data.name
      }
    }
  },
  mounted () {
    this.getModule()
  }
}



