import axios from 'axios'
export default {
  name: 'newMission',
  data () {
    return {
      mission: {
        creatorID: parseInt(localStorage.getItem('id')),
        name: '',
        startTime: '',
        endTime: '',
        content: '',
        moduleID: parseInt(sessionStorage.getItem('moduleId')),
        target: '',
        participants: []
      }
    }
  },
  methods: {
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
    },
    beforeRemove (file, fileList) {
      return this.$confirm(`确定移除${file.name}？`)
    },
    getStudentList () {
      axios.get(this.url + '/user/level/3')
        .then(this.getStudentListSucc)
    },
    getStudentListSucc (res) {
      res = res.data
      if (res) {
        this.StudentList = res
      }
    },
    submit () {
      // this.$refs.upload.submit()
      this.$axios({
        method: 'post',
        url: this.url + '/mission',
        data: JSON.stringify(this.mission)
      }).then((res) => {
        // this.$refs.upload.submit()
        if (res.status === 200) alert('成功创建任务！')
        else alert(res)
      }).catch(() => {
        alert('创建失败！')
      })
    },
    mounted () {
      this.getStudentList()
    }

  }
}



